'use strict';

import crypto from 'crypto';
import mongoose from 'mongoose';
mongoose.Promise = require('bluebird');
import {Schema} from 'mongoose';
import config from '../../config/environment';

const authTypes = ['github', 'twitter', 'facebook', 'google'];

let experienceSchema = new Schema({
  position: {
    type: String,
    required: true
  },
  company: {
    type: String,
    required: true
  },
  project: String,
  client: String,
  startDate: {
    type: Date,
    required: true
  },
  skills: {
    type: Array,
    default: []
  },
  endDate: Date,
  isCurrentPosition: Boolean,
  activityDescription: String
});

let skillSchema = new Schema({
  name: { type: String, required: 'Skill Name is a required field.', trim: true },
  experienceYears : { type: Number, default: 0 }
});

let educationSchema =  new Schema({
  school: {
    type: String,
    required: 'School Name is a required field.'
  },
  degree: {
    type: String,
    required: 'Degree is a required field.'
  },
  field: {
    type: String,
    required: 'Field is a required field.'
  },
  activities: String,
  startDate: {
    type: Date,
    required: 'Start Date is a required field.'
  },
  endDate: Date
});

let certificationSchema =  new Schema({
  name: {
    type: String,
    required: 'Certification Name is a required field.'
  },
  authority: {
    type: String,
    required: 'Certification Authority is a required field.'
  },
  startDate: Date,
  endDate: Date
});

let languageSchema =  new Schema({
  name: {
    type: String,
    required: 'Language is a required field.'
  },
  level: { type: Number, default: 1 }
});

let hobbySchema = new Schema({
  name: {
    type: String,
    required: 'Hobby name is a required field.'
  }
});
let positionSchema = new Schema({
  name: {
    type: String,
    required: 'Positiona name is a required field.'
  }
});
let UserSchema = new Schema({
    name: {
      type: String,
      trim: true
    },
    username: {
      type: String,
      unique: 'Username should be unique',
      required: 'Please fill in a username',
      trim: true
    },
    email: {
      type: String,
      lowercase: true,
      required: true
    },
    currentLocation: {
      type: String,
      trim: true
    },
    role: {
      type: String,
      default: 'user'
    },
    isActive: {
      type: Boolean,
      default: true
    },
    external_id: {
      type: String
    },
    skype: {
      type: String,
      default: ''
    },
    facebook: {
      type: String,
      default: ''
    },
    twitter: {
      type: String,
      default: ''
    },
    linkedIn: {
      type: String,
      default: ''
    },
    provider: String,
    salt: String,
	  google: {},
    currentClient: {
      type: String
    },
    currentProject: {
      type: String
    },
    profileComplete: {
      type: Boolean,
      default: false
    },
    aboutMe: {
      type: String,
      default: ''
    },
    summaryOfQualification: {
      type: String,
      default: ''
    },
    positions: [positionSchema],
    hobbies: [hobbySchema],
    experiences: [experienceSchema],
    skillsCloud: [skillSchema],
    education: [educationSchema],
    certifications: [certificationSchema],
    languageSkills: [languageSchema]
  }, {
  toJSON: {
    virtuals: true
  }
});

/**
 * Virtuals
 */

// Public profile information
UserSchema
  .virtual('profile')
  .get(function() {
    return {
	  'name': this.name,
    'role': this.role,
    'email': this.email,
	  'positions': this.positions,
	  'location': this.currentLocation,
	  'social': this.social,
    'picture': this.getProfilePicture(),
    'isProfileComplete': this.profileComplete,
    'username': this.username

    };
  });

UserSchema
  .virtual('completeProfile')
  .get(function() {
    return {
	  'name': this.name,
    'role': this.role,
    'email': this.email,
    'username': this.username,
	  'positions': this.positions,
	  'location': this.currentLocation,
	  'social': this.social,
    'picture': this.getProfilePicture(),
    'languageSkills': this.languageSkills,
    'certifications': this.certifications,
    'education': this.education,
    'summaryOfQualification': this.summaryOfQualification,
    'aboutMe': this.aboutMe,
    'skillsCloud': this.skillsCloud,
    'experiences': this.experiences,
    'hobbies': this.hobbies
    };
  });

//Once our model is changing, I'm creating this virtual prop to keep the contract unchanged
UserSchema
  .virtual('social')
  .get(function() {
    return {
	  'skype': this.skype,
    'facebook': this.facebook,
    'linkedIn': this.linkedIn,
    'twitter': this.twitter
    };
  });


// Non-sensitive info we'll be putting in the token
UserSchema
  .virtual('token')
  .get(function() {
    return {
      '_id': this._id,
      'role': this.role
    };
  });

/**
 * Validations
 */

// Validate empty email
UserSchema
  .path('email')
  .validate(function(email) {
    if (authTypes.indexOf(this.provider) !== -1) {
      return true;
    }
    return email.length;
  }, 'Email cannot be blank');

var validatePresenceOf = function(value) {
  return value && value.length;
};

/**
 * Methods
 */
UserSchema.methods = {

  /**
   * Convert a Google picture url to a local one if `proxyPictureRequest` is set to TRUE.
   *   Google Url: https://lh6.googleusercontent.com/-cDUEyHTb5Pk/AAAAAAAAAAI/AAAAAAAAAEA/6vTpsC0MmC4/photo.jpg?sz=50
   *   Local Url: /images/profile/lh6/-cDUEyHTb5Pk/AAAAAAAAAAI/AAAAAAAAAEA/6vTpsC0MmC4/photo.jpg?sz=200
   *
   * @return {String}
   * @api public
   */
  getProfilePicture: function() {
    let googleData = this.google || {},
        googleImage = googleData.image || {},
        profilePicture = googleImage.url || '',
        imagePathRegex = /^https?\:\/\/([^\.]+)\.googleusercontent\.com\/([^\?]+)(\?(.*))?$/;

    // Updating the 'sz' (size) parameter to 80 (default: sz=50)
    profilePicture = profilePicture.replace(/sz\=\d+/, 'sz=160');

    if(config.profile.proxyPictureRequest && imagePathRegex.test(profilePicture)) {
      let imagePathMatch = profilePicture.match(imagePathRegex);

      profilePicture = config.profile.proxyPictureUrl
        .replace('{host}', imagePathMatch[1])
        .replace('{imagePath}', imagePathMatch[2])
        .replace('{querystring}', imagePathMatch[4] || '');
    }

    return profilePicture;
  }
};

export default mongoose.model('User', UserSchema);
